body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

header {
  min-height: 10vh;
  background-color: #360816;
  color: #bbb;
}

header h2 {
  padding: 16px 0;
  margin-top: 0;
  font-size: 2.4rem;
  font-family: "Pacifico", cursive;
}

header h3 {
  padding-bottom: 16px;
}

@media (max-width: 768px) {
  header h2 {
    font-size: 2rem;
  }

  header h3 {
    font-size: 1rem;
  }
}

form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
}

button {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding: 12px 18px;
  font-family: "Roboto", sans-serif;
  background-color: black;
  color: #bbb;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1rem;
  border: 2px solid black;
  border-radius: 4px;
}

button:hover {
  background-color: #360816;
  border-color: #bbb;
}

.search-wrapper {
  margin: 0 auto;
  width: 250px;
}

.react-autosuggest__input {
  -webkit-flex-grow: 3;
          flex-grow: 3;
  padding: 12px 18px;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  border: 1px solid #aaa;
  border-radius: 4px;
}

.react-autosuggest__input--focused {
  outline: none;
}

.react-autosuggest__container {
  position: relative;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestions-container {
  display: none;
}

.react-autosuggest__suggestions-container--open {
  display: block;
  position: absolute;
  text-align: left;
  top: 35px;
  width: 99.25%;
  margin-top: 0.6rem;
  border: 1px solid #aaa;
  background-color: #fff;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  border-radius: 4px;
  z-index: 2;
  max-height: 428px;
  overflow-y: auto;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 12px 18px;
}

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd;
}

@media (max-width: 768px) {
  button,
  .react-autosuggest__input,
  .react-autosuggest__suggestions-container--open {
    font-size: 0.8rem;
  }
  button,
  .react-autosuggest__input {
    padding: 8px 14px;
  }
}

.result__neg-message {
  color: #360816;
  margin: 10px auto 0;
}

.result-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 0 auto;
  max-width: 1600px;
}

.result-wrapper {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  margin: 40px auto;
  min-width: 600px;
  border-radius: 50%;
  background-color: transparent;
  background-image: -webkit-radial-gradient(
    circle,
    rgba(128, 128, 128, 1) 10%,
    rgba(128, 128, 128, 0.1),
    rgba(128, 128, 128, 0)
  );
  background-image: radial-gradient(
    circle,
    rgba(128, 128, 128, 1) 10%,
    rgba(128, 128, 128, 0.1),
    rgba(128, 128, 128, 0)
  );
}

.result-wrapper h2,
.result-wrapper h3 {
  color: #360816;
}

.result-wrapper h3 {
  font-size: 1.5rem;
}

.result-wrapper h3 span {
  background-color: #360816;
  color: #bbb;
}

.result-wrapper img {
  width: 60px;
  height: 60px;
  -webkit-transition: 1s;
  transition: 1s;
}

.result-wrapper img:hover {
  -webkit-transform: scale(2, 2) rotate(360deg);
          transform: scale(2, 2) rotate(360deg);
}

.result-wrapper button {
  margin-top: 10px;
}

.result-map-wrapper {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-height: 600px;
}

.result-map-wrapper button {
  margin: 50px auto 0;
  max-height: 47px;
}

#result-map {
  margin: 20px auto 50px;
  width: 400px;
  height: 0px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

@media (max-width: 1080px) {
  .result-container {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .result-map-wrapper {
    height: auto;
  }
  .result-map-wrapper button {
    margin: 0 auto;
  }
}

@media (max-width: 768px) {
  .result-wrapper {
    min-width: 300px;
  }
  .result-wrapper h2,
  .result-wrapper h3 {
    font-size: 1.2rem;
  }
  #result-map {
    max-width: 95%;
  }
}

footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 5vh;
  background-color: #360816;
  color: #bbb;
  font-size: 0.8rem;
  font-family: "Pacifico", cursive;
  line-height: 5vh;
}

.app {
  width: 100%;
  min-height: 100vh;
  text-align: center;
  background-image: url(/static/media/rainbow-1909_1920.37e36d3f.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.app-container {
  min-height: 76vh;
  max-width: 100vw;
  margin: 0 auto;
}

